export function matchHeight() {
  'use strict';

  setMatchHeight('.js-match-height');

  function setMatchHeight(rootEl, options) {
    const rootElement = document.querySelectorAll(rootEl);
    let o = {
      sp: true,
      isFlex: true,
    };

    if (options) {
      for (let key in options) {
        o[key] = options[key];
      }
    }

    return rootElement.forEach( root => {
      /**
       * 
       * @constructor
       */
      const MatchHeight = function () {
        this.root = root;
        this.matchHeightItem = Array.prototype.slice.call(this.root.querySelectorAll(this.root.getAttribute('data-align-target')));
        this.sameHeighList = null;
      };

      MatchHeight.prototype = {
        init: function () {
          this.setTwoDimeAry();
        },

        /**
         * Y
         * @return {void}
         */
        setTwoDimeAry: function () {
          let pageYAry = [];

          // jscss
          setTimeout( () => {
            this.matchHeightItem.forEach( item => {
              let style = window.getComputedStyle(item);
              let marginTop = parseInt(style.marginTop.replace(/px/, ''));
              let paddingTop = parseInt(style.paddingTop.replace(/px/, ''));
              let itemYVal = item.getBoundingClientRect().top + marginTop + paddingTop;

              pageYAry.push(itemYVal);
              item.setAttribute('data-top', itemYVal);
            });
          }, 1);

          console.log(this.matchHeightItem)
        },
      };

      let matchHeight = new MatchHeight();
      matchHeight.init();
    });

  }
}
